#homebg {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
}

.container {
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin:0;
    left: 0px;
    top: 0px;
    z-index: -1000;
    overflow:hidden;
}

.overlay {
    position: absolute;
    top: 30%; 
    left: 20%;
    z-index: 1;
}

#welcome {
    color: green;
    font-size: 20px;
    align-self: center;
}

#home {
    position: relative;
    top: 4%; 
    left: 0%;
    height: 64px;
    height: 64px;
    z-index: 1000;
}

#github {
    position: relative;
    top: 2%; 
    left: 0.5%;
    height: 52px;
    height: 52px;
    z-index: 1000;
}